  font-family: 'Ubuntu', sans-serif;
  overflow-y:scroll;
}
#app-body{
  background: -webkit-gradient(linear, left top, right bottom, from(rgb(254, 255, 255)) , to(rgb(137, 212, 255)));
  background: linear-gradient(to bottom right, rgb(254, 255, 255) , rgb(137, 212, 255));
}
@media only screen and (min-width: 700px){
#app{
    display: grid;
    grid-template-columns: 20% 4fr;
    max-width: 100vw;
    margin: 0 auto;
    grid-template-areas: "app-nav app-body"
}
#app-nav{
    grid-area: 'app-nav';
    width: 100%;
    height: 100vh;
    text-align: center;
}
#app-body{
    grid-area: "app-body";
    border-left: 2px solid rgb(206, 223, 255);
}
.app-about{
    margin-bottom: 10em;
}
}
@media only screen and (max-width: 700px){
